.header {
    filter: var(--drop-shadow);
    color: var(--background-color-main);
}

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-color: var(--background-color-block);
    border-radius: .5rem;
    box-shadow: var(--shadow-window);
    padding: .5rem;
    margin-left: .5rem;
    background: var(--gradient);
}

.submit {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 5rem;
    margin: .5rem;
    padding: .2rem;
    border-radius: .2rem;
    color: var(--background-color-main);
    box-shadow: var(--shadow-line);
    background-color: var(--background-color-block);
    border: 0;
    transition: all ease-in .1s;
}

.submit:hover {
    box-shadow: none;
}

.buttons{
    display: flex;
    align-items: center;
}

button {
    height: fit-content;
}

.company, .position, .from, .until {
    display: flex;
    flex-direction: column;
}

input {
    width: 11rem;
}